import React from "react";
import { useObject } from "react-firebase-hooks/database";

import RenderHandler from "../components/RenderHandler";
import View from "../components/View";
import LineGraph from "../components/LineGraph";
import Card from "../components/Card";
import Text from "../components/Text";

const Graphs = props => {
  const [voltage] = useObject(props.firebase.database().ref("aVolt"));
  const [amphere] = useObject(props.firebase.database().ref("aAmp"));
  const [power, setPower] = React.useState();
  const [voltagedata, setVoltage] = React.useState();
  React.useEffect(() => {
    const currentVoltageData = voltage && voltage.val();
    const currentAmphereData = amphere && amphere.val();

    setVoltage(Math.round(currentVoltageData * 0, 98));
    setPower(Math.round(currentVoltageData * currentAmphereData * 115) / 100);
  }, [voltage, amphere]);

  return (
    <RenderHandler>
      {isDesktopOrLaptop => (
        <View
          flexDirection="column"
          flexAlignItems={!isDesktopOrLaptop && "center"}
        >
          <Text as="h3">Grafik Realtime</Text>
          <Card>
            <View
              flex={1}
              flexDirection={!isDesktopOrLaptop && "column"}
              flexAlignItems="center"
            >
              <LineGraph
                streamData={voltagedata}
                unit="V"
                title="TEGANGAN"
                height={180}
                options={{
                  scales: {
                    yAxes: [
                      {
                        ticks: {
                          beginAtZero: true,
                          min: 100,
                          max: 250,
                          stepSize: 10
                        }
                      }
                    ]
                  }
                }}
              />
              <LineGraph
                streamData={amphere && amphere.val()}
                unit="A"
                title="ARUS"
                height={180}
                options={{
                  scales: {
                    yAxes: [
                      {
                        ticks: {
                          beginAtZero: true,
                          min: 0,
                          max: 1.5,
                          stepSize: 0.1
                        }
                      }
                    ]
                  }
                }}
              />
              <LineGraph
                streamData={power}
                unit="VA"
                title="DAYA"
                height={180}
                options={{
                  scales: {
                    yAxes: [
                      {
                        ticks: {
                          beginAtZero: true,
                          min: 0,
                          max: 300,
                          stepSize: 20
                        }
                      }
                    ]
                  }
                }}
              />
            </View>
          </Card>
        </View>
      )}
    </RenderHandler>
  );
};

export default Graphs;
